#elevationWrapper {
  display: flex;
  flex-direction: column;
  gap: 16px;
}
.elevationItem {
  display: grid;
  grid-template-columns: 20px auto;
  font-size: 12px;
  font-style: italic;
  align-items: center;
  gap: 8px;
}
.elevation {
  width: 80px;
  height: 60px;
  background-color: var(--appBackground);
}
